* {
  margin: 0px;
  padding: 0px;
}
.footerBody {
  background-image: url("./Headimg.jpg");
  height: 50vh;
  padding: 25px;
  line-height: 40px;
  color: white;
}
.footerBody li {
  list-style: none;
}
.footerList {
  display: flex;
  justify-content: space-around;
}
.footerPar {
  text-align: center;
}
.fire {
  display: flex;
}
.fa {
  font-size: 2em;
}
/* Below is the styling of the responsiveness at 

different screen sizes */

@media screen and (max-width: 584px) {
  .footerList li {
    font: 14px sans-serif;
    padding: 10px;
  }
  .footerRule {
    margin-bottom: 35px;
  }
  .footerPar {
    font: 10px sans-serif;
  }
  .fire {
    display: flex;
    flex-direction: column;
  }
  .fa {
    font-size: 1.2em;
  }
}
a {
  color: blue;
  text-decoration: none;
}
